"use client";

import { useEffect } from "react";
import { useTheme } from "next-themes";

export default function FaviconSwitcher() {
    const { theme } = useTheme();

    useEffect(() => {
        const setIcons = async (resolvedTheme: string) => {
            const lightHref = "/favicon.svg";
            const darkHref = "/favicon.png"; // create this file in public/ for dark mode

            // prefer dark href when theme is dark; fall back to light if dark icon missing
            const preferredHref = resolvedTheme === "dark" ? darkHref : lightHref;
            let finalHref = preferredHref;

            if (resolvedTheme === "dark") {
                try {
                    const res = await fetch(darkHref, { method: "HEAD" });
                    if (!res.ok) finalHref = lightHref;
                } catch {
                    finalHref = lightHref;
                }
            }

            const setLink = (rel: string, hrefValue: string, sizes?: string) => {
                let selector = `link[rel='${rel}']` + (sizes ? `[sizes='${sizes}']` : "");
                let link = document.head.querySelector(selector) as HTMLLinkElement | null;
                if (!link) {
                    link = document.createElement("link");
                    link.rel = rel;
                    if (sizes) link.sizes = sizes;
                    document.head.appendChild(link);
                }
                link.href = hrefValue;
            };

            setLink("icon", finalHref, "32x32");
            setLink("icon", finalHref, "16x16");
            setLink("shortcut icon", finalHref);
            setLink("apple-touch-icon", finalHref);
        };

        const resolve = (t?: string) => {
            if (!t || t === "system") {
                const prefersDark = window.matchMedia && window.matchMedia("(prefers-color-scheme: dark)").matches;
                return prefersDark ? "dark" : "light";
            }
            return t as string;
        };

        setIcons(resolve(theme));

        // If using system theme, listen for changes
        let mql: MediaQueryList | null = null;
        const onChange = () => {
            if (theme === "system") setIcons(resolve("system"));
        };
        if (typeof window !== "undefined" && window.matchMedia) {
            mql = window.matchMedia("(prefers-color-scheme: dark)");
            if (mql.addEventListener) mql.addEventListener("change", onChange);
            else mql.addListener(onChange as any);
        }

        return () => {
            if (mql) {
                if (mql.removeEventListener) mql.removeEventListener("change", onChange);
                else mql.removeListener(onChange as any);
            }
        };
    }, [theme]);

    return null;
}
